const setupSticky = (selector, height = 0) => {
  let options = {
    rootMargin: `-${height}px 0px 0px 0px`,
    threshold: [0.0, 0.5, 1.0]
  };

  let observer = new IntersectionObserver((entries) => {
    if (!entries[0].isIntersecting) {
      entries[0].target.parentElement.querySelector(".sticky").classList.add("stuck")
    } else {
      entries[0].target.parentElement.querySelector(".sticky").classList.remove("stuck")
    }
  }, options);

  let targets = document.querySelectorAll(selector);

  for(let target of targets) {
    const separator = target.querySelector(".separator") || document.createElement("div");
    separator.classList.add("separator");
    target.insertBefore(separator, target.firstChild);

    for (let i of target.querySelectorAll(".sticky")) {
      i.style.top = `${height}px`;
    }

    observer.observe(separator);
  }

  return () => {
    for(let target of targets) {
      observer.unobserve(target.querySelector(".separator"));
      observer.disconnect();
    }
  }
};

const setupHeaders = () => {
  const header = document.querySelector("header.main");
  const {height} = header.getBoundingClientRect();

  return setupSticky(".sticky-container", height);
};

let destroy = setupHeaders();

window.addEventListener("resize", () => {
  destroy();
  destroy = setupHeaders();
});

import stickybits from 'stickybits'

stickybits('header.main', {
  useStickyClasses: true
});
